.section-blog {
    background-color: #F6F5F2;
    padding: 10rem 0;
 
    &__btn{
        display: flex;
        justify-content: space-between;
        margin-top: 3rem;

        &__next{
            margin-right: 3rem;
        }
        &__prev{
            margin-left: 3rem;
        }
    }

    @include respond(phone) {
        padding: 1rem 0 2rem 0;
    }
}

.section-register {
    display: flex;
    justify-content: center;
    background-color: #F8F6E3;
    padding: 10rem 0;
    flex:1;
}

.section-footer {
    background-image: linear-gradient(to right bottom, var(--color-primary-light), var(--color-primary-dark));
    color: #fff;
    padding: 20px 0;
    height: 10rem;
 
    width: 100%;
    bottom: 0;
  

    &__container {
        width: 80%;
        margin: 0 auto;
        text-align: center;
    }
}

.section-add_blog{
    padding: 10rem 3rem;
    background-color: #F8F6E3;
}

.section-blogpage{
    padding: 5rem 8rem;
    background-color: #F8F6E3;

    &__short{
        margin-bottom: 30rem;
    }

    &__long{
        margin-bottom: 0rem;
    }

    @include respond(phone) {
        padding: 3rem 5rem;
     }

    &__title{
        display: flex;
        justify-content: center;
        font-size: 2rem;
        margin-bottom: 3rem;

        @include respond(phone) {
            margin-bottom: 1.5rem;
            font-size: 1.5rem;
         }
    }

    &__subtitle{
        font-size: 2rem;
        margin-bottom: 3rem;

        @include respond(phone) {
            font-size: 1.5rem;
         }
    }

    &__content{
        font-size: 2rem;
        @include respond(phone) {
           font-size: 1rem;
           flex: 1;
        }
    }
}



