.register {
    box-shadow: var(--shadow-dark-register);
    width: 100rem;
    margin-top: 2rem;
    height: 55rem;
    display: flex;
    justify-content: space-between;

    & input {
        border: 1px solid #ccc;
        padding: 8px;
        margin-top: 1rem;
        height: 3rem;
    }

    &__image {
        flex: 0 0 40%;
    }

    &__text {
        flex: 1;

        &__heading {
            font-weight: 400;
            margin-top: 5rem;
            font-size: 3rem;
            text-align: center;
        }

    }

    &__form {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding-bottom: 4rem;


        &__input {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            font-size: 2rem;
            margin: 1rem 8rem;
        }

        &__button {
            display: flex;
            justify-content: flex-end;
            margin-top: 2rem;
            margin-right: 8rem;
          
           &-btn{
            padding: 0.5rem 2rem;
            border-radius: 0.5rem;
            text-decoration: none;
            border: 2px solid #333;
            font-size: 2rem;
            transition: all 0.2s ease;

            &:hover {
                background-color: var(--color-primary-dark);
            }
           }
         
        }

    }

    &__image-img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
    }
}