.add-blog {

    background-color: whitesmoke;
    border-radius: 2rem;
    padding: 2rem;
    border: 2px solid #ccc;

   


    &__heading {
        padding-left: 2rem;
        color: black;
        font-weight: 500;
        margin-bottom: 6px;
    }
    &__line{
        border: none;
        height: 1px;
        background-color: black;
    }

    &__form {

        &__btn-container{
            display: flex;
            justify-content: flex-end;
        }

        &__button{
            padding: 0.5rem 2rem;
            border-radius: 0.5rem;
            text-decoration: none;
            border: 2px solid #333;
            font-size: 2rem;

            @include respond(phone) {
                padding: 0.5rem 1rem;
                font-size: 1.5rem;
            }


        }

        &__group{
            width: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            font-size: 2rem;
            margin: 2rem 0;

            &__label{
                font-size: 1.5rem;
                font-weight: 400;
                margin-bottom: 3px;
            }

            &__input{
                padding: 6px;
                border: 2px solid #ccc;
                border-radius: 0.5rem;
               

                &-file{
                    width: 20%;
                    padding: 1.5rem;

                    @include respond(phone) {
                        width: 50%;
                        padding: 6px;
                    }

                }
            }


        }
        }
        
}