.blog-data{
    display:flex;
    flex-direction: column;
    justify-content: center;
    margin:  4rem 15rem;
    padding: 2rem;
    transition: all 0.2s ease;
    position: relative;

    @include respond(phone) {
        margin:  4rem 6rem;
    }

    &:hover{
        transform: translateY(-0.1rem);
        background-color: rgb(244, 229, 224);
        box-shadow: var(--shadow-dark-blog);
    }

    &:first-child{
        margin-top: 0rem;
    }

    &:last-child{
        margin-bottom: 0;
    }

    &__text{
        text-decoration: none;
        color:black;
        transition: all .2s ease;

        &__title {
            font-weight: 500;
            font-size: 4rem;

            @include respond(phone) {
                font-size: 2rem;
            }
        }

        &__subtitle {
            font-weight: 500;
            font-size: 2rem;
            margin-bottom: 1rem;

            @include respond(phone) {
                font-size: 1.5rem;
            }
        }

        &--animate{
            &:hover{
                color: var(--color-primary-light);
            }
        }
    }

    &__detail{
        font-size: 2rem;
        font-weight: 200;
        margin-bottom: 1rem;

        @include respond(phone) {
            margin-bottom: 0;
            font-size: 1rem;
        
        }
    }
     

    &-link__container{
        display: flex;
        justify-content: end;

        @include respond(phone) {
            position: relative;
            bottom: 1.5rem;
        }
      

        &__delete {
            text-decoration: none;
            color: black;
            font-size: 2rem;
            padding: 1rem;
            width: fit-content;
     

            @include respond(phone) {
                font-size: 1rem;
            }

            &:hover{
                color: var(--color-grey-dark-3);
            }
        }
        

        &__link{
            text-decoration: none;
            color: black;
            font-size: 2rem;
            padding: 1rem;
            width: fit-content;
            transition: all 0.2s ease;

            @include respond(phone) {
                font-size: 1rem;
            }

            &:hover{
                color: var(--color-grey-dark-3);
            }
        }

    }
   

  
}