.header {
    height: 58vh;
    position: relative;

    @include respond(phone) {
        height: 30vh;
    }
    
    &__img_container{ 
        position: absolute;
        width: 100%;
        height: 100%;
        z-index: -3; 
        

        &__heading{
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 80%;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            z-index: 10;

             
            @include respond(phone) {
                margin-left: 0rem;
                width: 100%;
            }

            &-text{
                color: white; 
                opacity: 0.6;
                font-weight: 900;
                font-size: 15rem;

                @include respond(phone) {
                    font-size: 6rem;
                }
            }

            &-msg{
                color: white; 
                opacity: 0.6;
                font-size: 4rem;

                @include respond(phone) {
                    font-size: 2rem;
                }
            }
          
    }
}

    &__img_container img{
    width: 100%; 
    height: 100%;
    object-fit: cover;

    }

    &__logo-box {
        position: absolute;
        top: 4rem;
        left: 4rem;
    }

    &__logo {
        height: 3.5rem;
    }

    &__text-box {
        position: absolute;
        top: 40%;
        left: 50%;
        transform: translate(-50%, -50%);
        text-align: center;
    }

}

.uploadimage{

    &__button{
        position: absolute;
        top: 50%;
        left: 85%;
        padding: 8px 10px;
        font-size: 1.6rem;
        background-color: #007bff;
        color: #fff;
        border: none;
        cursor: pointer; 
        
        &:hover{
            background-color: #0056b3;
        }
    }
}

